import './App.css';
import {BrowserRouter, Route, Routes} from 'react-router-dom'
import Main from './Pages/Main'
import Show from './Pages/Show'
import Register from './Pages/Register'
import Post from './Pages/Post'
import Cancel from './Pages/Cancel'
import Success from './Pages/Success'
import About from './Pages/About'
import Delete from './Pages/DeletePosts'
import Terms from './Pages/Condition'
import Policy from './Pages/Policy';
import Page from './Pages/HomePage'
import PremiumRegister from './Pages/Premium_Register'
import Premium_Post from './Pages/Premium-Post'

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Page />} />
        <Route path="/home" element={<Main />} />
        <Route path="/product/:id" element={<Show />} />
        <Route path="/register" element={<Register />} />
        <Route path="/post" element={<Post />} />
        <Route path="/payment/cancel" element={<Cancel />} />
        <Route path="/payment/Success/:html/:css/:js" element={<Success />} />
        <Route path="/about" element={<About />} />
        <Route path="/delete" element={<Delete />} />
        <Route path="/termsconditions" element={<Terms />} />
        <Route path="/privacypolicy" element={<Policy />} />
      </Routes>
    </BrowserRouter>
  );
}

//        <Route path="/premiumregister" element={<PremiumRegister />} /><Route path="/premium-post" element={<Premium_Post />} />

export default App;
