import { message, Row, Col} from 'antd'
import axios from 'axios'
import { useState } from 'react'
import NavBar from '../Components/NavBar'
import "./delete.css"
import DeleteCard from '../Components/DeleteCard'
import Alert from 'react-bootstrap/Alert';


function Delete(){
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [posts, setPosts] = useState([])

        const SearchButton = () => {
            axios.get(`/acc/find/${username}/${password}`).then((res) => {
                if(res.data.length === 0){
                    alert("Account was not found")
                }
                else{
                    axios.get(`/get/code/${username}`).then((res2) => {
                        setPosts(res2.data)
                    }).catch(error => alert("Error"))
                }
            })
        }
    
    return(
        <NavBar>
             <div class="container-d">
                    <div class="form-box-d">
                        <h1>Enter details to Find your sales</h1>
                            <div class="input-group-d">
                                <div class="input-field-d">
                                    <i class="fa-solid fa-user"></i>
                                    <input type="text" placeholder="User Name" onChange={(e) => setUsername(e.target.value)} />
                                </div>

                                <div class="input-field">
                                    <i class="fa-solid fa-lock"></i>
                                    <input type="password" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                                </div>
                            </div>
                            <div class="button-field-d">
                                <button onClick={SearchButton}>Find Sales</button>
                            </div>
                    </div>
                </div>
                <div>
                    <Row>
                        {posts.map((products) => (
                            <Col xs={24}  sm={6} md={12} lg={6} >
                                <DeleteCard products={products} />
                            </Col>
                        ))}
                    </Row>
                </div>
        </NavBar>
    )
}

export default Delete