import './home.css'
import { useNavigate } from 'react-router-dom'

function Main(){
    const navigate = useNavigate();
    return(
        <div className="banner">
            <div className="navbar">
                <h2 class="logo">Code Market</h2>
                <ul>
                    <li><a href="/home">Home</a></li>
                    <li><a href="/post">Add Sales</a></li>
                    <li><a href="/delete">Scan for your sales</a></li>
                    <li><a href="/about">About</a></li>
                </ul>
                <ul>
                    <li><a href="/register">Register</a></li>
                </ul>
            </div>
            <div className="content">
                <h1>Code Market</h1>
                <p>Code market, a web application to sell and buy HTML, CSS and JS code.</p>
                <button onClick={() => navigate("/home")}><span></span>Go to main</button>
            </div>
        </div>
    )
}

export default Main