import NavBar from '../Components/NavBar'
import {useState, useEffect} from 'react'
import axios from 'axios'
import {Row, Col} from 'antd'
import Products from '../Components/Products'
import "./main.css"
import ReactDOM  from 'react-dom'
import Search from '../Components/Search'

function Home(){
    const [code, setCode] = useState([])
    const [category, setCategory] = useState('')

    const root = ReactDOM.createRoot(
        document.getElementById('root')
      );

    useEffect(() => {
        const getCode = async() => {
            try{
                const {data} = await axios.get("/code/get")
                setCode(data)
            }catch(error){
                alert("Error")
            }
        }
        getCode()
    }, [])

    const item = async() => {
        try{
            const {data} = await axios.get(`/found/${category}`);
            const searched_items = <Search product={data} />
            root.render(searched_items)
        }catch(error){
            alert("Search items were not found")
        }
    }

    return(
        <NavBar>
                <div className="container-main">
                    <p className="hanger">Item</p>
                    <div className="slider">
                        <div className="search">
                            <input className="searchbar" onChange={(e) => setCategory(e.target.value)} placeholder="search for sales....."></input>
                            <div className="search-button">
                                <button onClick={item}><i class="fa-solid fa-magnifying-glass"></i></button>
                            </div>
                        </div>
                    </div>
                </div>
                <p className="holder">Item</p>
                <div className="product-display">
                    <Row>
                        {code.map((products) => (
                            <Col xs={24}  sm={6} md={12} lg={6} >
                                <Products className='products' products={products} />
                            </Col>
                        ))}
                    </Row>
                </div>
                <footer>
                    <p>© Copyright 2023, Code Market</p>
                </footer>
        </NavBar>
    )
}

export default Home