import NavBar from '../Components/NavBar'
import { Helmet } from 'react-helmet'

function Cancel(){
    return(
        <div>
            <NavBar>
                Payment could not be made
                <Helmet>
                    <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-8917170152355472"
                    crossorigin="anonymous"></script>
                </Helmet>
            </NavBar>
        </div>
    )
}

export default Cancel