import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import ViewCode from './ViewCode'
import ReactDOM from 'react-dom'
import { Avatar } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import axios from 'axios'
import { useEffect, useState } from 'react'
import "./param.css"
import Comment from './Comment'

function Params({product}){
    const [profile, setProfile] = useState('')

    const [username, setUsername] = useState('')
    const [userpassword, setPassword] = useState('')
    const [comment, setComment] = useState('')

    const pay = () => {
        axios.post("/pay", {name: product.name, APIkey: product.APIkey, html: product.html, css: product.css, js: product.js, price: product.price}).then((response) => {
            window.location.href = response.data
        }).catch(error => alert("Error, could not make payment, user has entered wrong API key"))
    }
    
    const runCode = () => {
        ReactDOM.render(<ViewCode html={product.html} css={product.css} js={product.js} name={product.name} />, document.getElementById('root'))
    }
    
    useEffect(() => {
        axios.get(`/get/Computer4062`).then((res) => {
            setProfile(res.data.profile)
        }).catch(error => alert("Error while loading profile"))
    }, [])  

    useEffect(() => {
        const run = () => {
            const htmlCode = product.html;
            const cssCode = "<style>" + product.css + "</style>";
            const jsCode = product.js;
            const output = document.querySelector(".run #myiframe");
            
            output.contentDocument.body.innerHTML = htmlCode+cssCode;
            output.contentWindow.eval(jsCode)
        }
        run()
    }, [])

    const postComment = async() => {
        axios.get(`/acc/find/${username}/${userpassword}`, {username, userpassword}).then((response) => {
            if(response.data.length === 0){
              alert("Account was not found")
            }else{
              alert("Account found, commented")
              axios.post("/comment/post", {username: username, password: userpassword,comment: comment, name: product.name}).then((res) => {
                  alert("Comment was send")
              }).catch(error => console.log(error))
            }
        }).catch(error => {alert("Error"); console.log(error)})
    }

    return(
        <Row>
            <Col md={6}>
                <div class="cover-image">
                    <div className="run">
                        <iframe className='compiler frame' title={product.name} id="myiframe" style={{height: 400}}></iframe>
                    </div>
                </div>
                <Col class="form-box-c">
                    <h1>Commments</h1>
                    <form>
                        <div class="input-group-c">
                            <div className="input-group-2-c">
                                <div class="input-field-c">
                                    <i class="fa-solid fa-user"></i>
                                    <input type="text" placeholder="User Name" onChange={(e) => setUsername(e.target.value)} />
                                </div>
                                <div class="input-field-c">
                                    <i class="fa-solid fa-lock"></i>
                                    <input type="password" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                                </div>
                            </div>
                            <div class="input-field-c">
                                <i class="fa-solid fa-envelope"></i>
                                <input type="text" placeholder="Comment" onChange={(e) => setComment(e.target.value)} />
                            </div>
                        </div>
                        <div class="field-button">
                            <button onClick={postComment}>Comment</button>
                        </div>
                    </form>
                    <Comment name={product.name} />
                </Col>
            </Col>
            <Col md={3}>
                <div className='details-p'>
                <div className="title-p">
                    <h1>{product.name}</h1>
                </div>

                <div className="description-p">
                    <p>{product.description}</p>
                </div>

                <div className="price-p">
                    <strong><i class="fa-solid fa-dollar-sign"></i>{product.price}</ strong>
                </div>

                <div className="profile-p">
                    <p><i class="fa-solid fa-user"></i>Made By: <Avatar shape="square" src={profile} size={20} icon={<UserOutlined />} />{product.username}</p>
                </div>

                <div className="button-p" >
                    <button onClick={runCode} >Run Code On FullScreen</button>
                </div>
                <div className="button-p">
                    <button onClick={pay} >Purchse Code</button>
                </div>
                </div>
            </Col>
        </Row>
    )
}

export default Params