import React, { useState } from 'react';
import NavBar from '../Components/NavBar'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import "./register.css"

const Register = () => {

  const navigate = useNavigate()

  const Register = async () => {
    navigate("/home")
    try{
      const {acc} = await axios.post("/acc/create", {username: username, userpassword: userpassword, profile: profile})
      navigate("/home"); 
      alert("Account created")
    }catch(error){
      alert("Username and password was already taken")
    }
  };

  const [username, setUsername] = useState('')
  const [userpassword, setUserPassword] = useState('')
  const [profile, setProfile] = useState('')

  return (
      <NavBar>
        <div class="container-r">
          <div class="form-box">
              <h1>Sign up</h1>
              <form>
                  <div class="input-group">
                      <div class="input-field">
                          <i class="fa-solid fa-user"></i>
                          <input type="text" placeholder="User Name" onChange={(e) => setUsername(e.target.value)} />
                      </div>
                      <div class="input-field">
                          <i class="fa-solid fa-envelope"></i>
                          <input type="text" placeholder="Profile Image url" onChange={(e) => setProfile(e.target.value)} />
                      </div>
                      <div class="input-field">
                          <i class="fa-solid fa-lock"></i>
                          <input type="password" placeholder="Password" onChange={(e) => setUserPassword(e.target.value)} />
                      </div>
                  </div>
                  <p>By clicking signup you have agreen to the <a href="termsconditions">terms and conditions</a> of Code Market</p>
                  <div class="button-field">
                      <button onClick={Register}>Sign up</button>
                  </div>
              </form>
          </div>
      </div>
      </NavBar>
  );
};
export default Register;