import "./Navbar.css"

function NavScrollExample({children}) {
  return (
    <div>
        <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.2.3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-rbsA2VBKQhggwzxH7pPCaAqO46MgnOM80zW1RWuH61DGLwZJEdK2Kadq2F9CUG65" crossOrigin="anonymous" />
        <div className="navbar-h" style={{paddingTop: 10, paddingLeft: 50, paddingRight: 15}}>
            <h2 className="logo-h" style={{fontWeight: 'bold'}}>Code <span className="heading">Market</span></h2>
            <ul>
                <li><a href="/home">Home</a></li>
                <li><a href="/post">Add Sales</a></li>
                <li><a href="/delete">Scan for Sales</a></li>
                <li><a href="/about">About</a></li>
            </ul>
            <ul>
                <button className="register">Sign Up</button>
            </ul>
        </div>
        {children}
    </div>
  );
}

export default NavScrollExample