import axios from 'axios'
import { message } from 'antd'

function DeleteCard({products}){

    const deletePost = async(id) => {
        try{
            await axios.post("/code/delete", {_id: id})
            alert("Removed")
        }catch(err){
            message.error("Error")
            console.log(err)
        }
      }

    return(
        <div className="container">
            <div className="content-section">
                <div className="card">
                    <img src={products.image} className="image-card" style={{height: 290, width: 350}}/>
                    <h2>{products.name}</h2>
                    <p>{products.description}</p>
                    <div className="delete-button">
                        <button onClick={deletePost(products.id)}>Delete</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default DeleteCard