import './product.css'

function Product({products}) {
  const click = () => {
    window.location.pathname = `/product/${products._id}`
  }
  
  return (
      <div className="container" onClick={click}>
        <div className="content-section">
          <div className="card">
            <img src={products.image} style={{height: 290, width: 350}}/>
            <h2>{products.name}</h2>
            <p>{products.description}</p>
            <strong style={{fontSize: 20}}>${products.price}</strong>
          </div>
        </div>
      </div>
  );
}

export default Product;